.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.row, #instafeed, .forms-formBlock, .comments-box {
  max-width: 90rem;
  padding: 3%; }

.masonry-css {
  column-count: 1;
  column-gap: 3rem; }
  @media screen and (min-width: 37.5em) {
    .masonry-css {
      column-count: 2; } }
  @media screen and (min-width: 50em) {
    .masonry-css {
      column-count: 3; } }

.masonry-css-item {
  break-inside: avoid;
  margin-bottom: 3rem; }

#masonry-grid:after {
  content: '';
  display: block;
  clear: both; }

.left {
  padding: 40px; }

.right {
  padding: 40px; }

.text {
  padding: 0px;
  text-align: center; }

.icon {
  color: #840b34;
  font-size: 30px;
  padding: 0 10px; }

@media screen and (min-width: 801px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    width: 100%; }
  .left {
    width: 58%; }
  .right {
    width: 42%; }
  .text {
    text-align: left;
    margin-top: 20px;
    padding: 0px 150px 20px 0px; } }

html {
  font: normal normal 200 1rem/1.5 "Gill Sans", "Gill Sans MT", Calibri, sans-serif; }

.vertical-center {
  display: flex;
  align-items: center;
  justify-content: center; }

footer {
  width: 100%; }

label.field {
  border-radius: 2px;
  color: #222;
  display: block;
  margin: 0 0 1rem;
  padding: 8px 0;
  opacity: 0;
  position: relative;
  transition-property: opacity;
  z-index: 1; }
  label.field span {
    color: #222;
    display: block;
    font-size: 16px;
    height: 20px;
    line-height: 20px;
    left: 9px;
    pointer-events: none;
    position: absolute;
    top: 32px;
    transform: scale(1) translateY(0);
    transition-property: color, font-size, top;
    z-index: 1; }
    label.field span.required::after {
      color: inherit;
      content: "*";
      display: block;
      height: 20px;
      left: -20px;
      line-height: 20px;
      position: absolute;
      text-align: center;
      top: 0;
      width: 20px; }
    .error label.field span {
      color: #F02318; }
  label.field .psuedo_select {
    background: rgba(255, 255, 255, 0);
    position: relative;
    border-bottom: 1px solid #eed0c8;
    color: #0a0a0a;
    cursor: pointer;
    font-family: inherit;
    font-size: 1rem;
    font-weight: 300;
    line-height: 1.5;
    height: 24px;
    margin: 24px 0 0 0;
    min-width: 250px;
    padding-top: 30px;
    outline: 0;
